@import './colors.scss';
@import './fonts.scss';

.home {
    background-color: $background;

    .home-header {
        width: 80%;
    }

    .choices-container {
        margin-top: 45px;
        width: 80%;
    }

    .wallet-connect-container {
        /* Auto layout */
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 20px;
        margin-bottom: 20px;

        background: linear-gradient(119.36deg, #007FFF 0%, #0059B2 100%);
        border-radius: 8px;
    }

    .wallet-description-container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 25px;
    }

    .wallet-connect-description {
        font-family: 'roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        display: flex;
        color: #FFFFFF;
        text-align: left;
        margin: 8px 0px;
    }

    .wallet-buy-url {
        font-family: 'roboto';
        font-size: 14px;

        color: #FFFFFF;

        /* Inside auto layout */

        margin: 8px 0px;
    }

