@import './fonts.scss';
@import './colors.scss';

.overview-container {

    .title {
        font-family: roboto;
        font-size: 16px;
        line-height: 21px;
        text-align: center;
        color: $text-primary;

        margin-bottom: unset;
    }
    
    .icon-container {
        height: 55px;

        .total {
            font-family: roboto;
            font-size: 24px;
            line-height: 32px;
            color: $text-primary;
        }
    
        .coin-img {
            margin: 0 10px;
        }
    
        .spaces {
            font-family: roboto;
            font-size: 12px;
            line-height: 16px;
            color: $text-yellow;
            text-transform: uppercase;
        }
    }
}