@import '../css/colors.scss';
@import '../css/fonts.scss';


.game-choice-card {
    background-color: $blue-three;

    .game-choice-content {
        text-align: left !important;

        .title {
            font-family: roboto;
            font-size: 24px;
            line-height: 32px;
            color: $text-primary;
        }

        .game-description {
            font-family: roboto;
            font-size: 14px;
            line-height: 18px;
            color: $text-secondary;
        }

        .bet {
            font-family: roboto;
            font-size: 12px;
            line-height: 16px;
            color: $text-secondary;

            margin-top: 20px;
        }

        .MuiCardActions-root {
            padding-left: unset;
        }
    }
}

#dialogContainer:nth-child(3):nth-child(1) {
    border-radius: 8px;
}

.NotEnoughAces{
 /* Auto layout */

    color:white;
    background: #132F4C;
    /* Border/Card Border */
    border: 1px solid #1E4976;
    box-sizing: border-box;

    #alert-dialog-slide-description{
        color:white;

        a{
            color:white;
        }
    }

    .actionButton{
        // background-color: white;
        // width:40px;
        font-weight: 900;
    }
}