.flex-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.column-flex {
    flex-direction: column;
}

.space-between {
    justify-content: space-between;
}

.space-around {
    justify-content: space-around;
}

.space-evenly {
    justify-content: space-evenly;
}

.start {
    align-items: flex-start;
}

.hor-start {
    justify-content: flex-start;
}