@import './fonts.scss';
@import './colors.scss';

.game-type-container {
    .game-type-label {
        font-size: 12px;
        line-height: 16px;
        font-family: roboto;
        color: $text-secondary;
        margin-bottom: unset;
    }
    .game-type {
        background: $blue-three;

        border: 1px solid $card-border;
        border-radius: 8px;
        margin: 8px 0px;
        width: 128px;
        height: 32px;

        color: $text-primary;
        font-size: 12px;
        line-height: 16px;
        font-family: roboto;

        option {
            background: $blue-three;

            border: 1px solid $card-border;
        }
    }

    margin-right: 20px;
}
