@import './fonts.scss';
@import './colors.scss';


html {
    overflow-x: hidden;
}

.table-container {
    width: 100vw;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    .game-header {}

    .table {
        width: 1155px;
        height: 600px;
        padding: 0 15%;
        height: 620px;
        border: 32px solid #173A5E;
        box-sizing: border-box;
        box-shadow: inset 0px 3px 0px rgba(0, 0, 0, 0.25), inset 0px 3px 0px rgba(255, 255, 255, 0.08);
        background: $blue-three;

        border-radius: 1000px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .dealer-side {
            // font-family: 'Roboto Mono';
            position: absolute;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            color: #5090D3;
            left: calc(50% - 65px/2 + 0.5px);
            top: calc(50% - 24px/2 - 295px);
        }

        .dealer-cards {
            width: 665px;
            height: auto;
            display: flex;
            justify-content: space-evenly;
            flex-direction: row;
            .dealer-first-card {
                margin-right:30px;
            }
            .dealer-last-card {
                // margin-left:30px;
            }
            .dealer-card {
                width:140px;
                height:140px;
                display: flex;
                justify-content: center;

                .dealer-card-title {
                    color: #265D97;
                    font-size: 12px;
                    font-family: roboto;
                }

                .dealer-card-pile {
                    width: 100px;
                    height: 140px;
                    position: relative;   
                    margin-left: 10px;
                    margin-right: 10px;                 
                    
                    .dealer-card-rotate {
                        transform: rotate(8deg);
                    }
    
                    .dealer-real-card-pile {
                        position: absolute;
                        width: 100px;
                        height: 140px;
                        top: 0;
                        left: 0;
                        border-radius: 8px;
                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    }
                    .dealer-real-card-pile-override {
                        position: absolute;
                        width: 100px;
                        height: 140px;
                        top: 0;
                        left: 0;
                        border-radius: 8px;
                    }
                }

                .dealer-card-image-pile{
                    width: calc(140px + 382 / 2000 * 140px);
                    height: calc(140px + 158 * 2 / 2000 * 140px);
                    margin-top:-12px;
                }
    
            }
            .dealer-in-play {
                width: 100px;
                height: 140px;
                border: 2px dashed #1E4976;
                box-sizing: border-box;
                border-radius: 8px;
                box-shadow: none !important;
                margin-left:30px;
            }
        }

        .table-middle-section {
            width: 665px;
            height: 132px;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
           

            .boundary-line {
                width: 100%;
                height: 1px;
                margin-left:5%;
                margin-right: 5%;
                color: $card-border;
            }
            .pot-title {
                line-height: 32px;
                font-size: 24px ;
                font-weight: 500;
                color: #FFD600;
                width: max-content;

            }
            .pot-section {
                display: flex;
                align-items: center;
            }
            .pot-coin {
                margin-left: 8px;
                width: 24px;
                height: 24px;
            }
        }
        .your-first-card {
            margin-right:30px;
        }
        .your-last-card {
            margin-left:30px;
        }
        .your-cards {
            width: 665px;
            height: auto;
            display: flex;
            justify-content: space-evenly;

            .your-card {
                z-index: 30;
                .your-card-title {
                    color: #265D97;
                    font-size: 12px;
                    font-family: roboto;
                    width: 140px;
                    height: 16px;
                }
                .your-card-dynamic {
                    flex-grow: 5;
                }
                .your-card-pile {
                    width: 140px;
                    height: 140px;
                    position: relative;
                    
                    .your-card-rotate {
                        transform: rotate(-8deg);
                    }
    
                    .your-real-card-pile {
                        position: absolute;
                        max-width: 140px;
                        height: 140px;
                        top: 0;
                        left: 0;
                        border-radius: 8px;
                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    }
                }
    
            }
            .your-in-play {
                position: absolute;
                width: 140px;
                height: 140px;
                top:0px;
                left: 0px;
                border: 2px dashed #1E4976;
                box-sizing: border-box;
                border-radius: 8px;
                box-shadow: none !important;
            }
            .your-card-1 {
                margin-left: -30px;
                z-index: 25;
            }
            .your-card-2 {
                margin-left: -30px;
                z-index: 20;
            }
            .your-card-3 {
                margin-left: -30px;
                z-index: 15;
            }
            .your-card-4 {
                margin-left: -30px;
                z-index: 10;
            }
        }
        .game-result-notification {
            position: absolute;
            left: calc(50% - 116px/2 + 12px);
            top: calc(50% - 32px/2 + 250px);
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
        }

        .game-result-notification-success{
            color: #00E676;
        }

        .game-result-notification-lost{
            color: #990000;
        }

        .your-side {
            position: absolute;
            left: calc(50% - 33px/2 + 0.5px);
            top: calc(50% - 24px/2 + 295px);
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            color: #5090D3;
        }
        .your-earned {
            position: absolute;
            background: rgba(10, 25, 41, 0.72);
            /* Border/Border Brighter */

            border: 1px solid #66B2FF;
            box-sizing: border-box;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 0px 20px 25px rgba(0, 0, 0, 0.1);
            backdrop-filter: blur(20px);
            /* Note: backdrop-filter has minimal browser support */

            border-radius: 100px;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            color: #80BFFF;
            padding: 6px;
            min-width: 90px;
            // height: 50px;
            left: calc(50% - 33px/2 + 270px);
            top: calc(50% - 20px/2 + 280px);
        }
       
    }
    .table-5 {
        width: 1280px !important;
    }
}

.your-real-card-pile-override {
    position: absolute;
    width: 140px;
    height: 140px;
    top: 0;
    left: 0;
    border-radius: 8px;
}

.game-footer-container {
    width: 100vw;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: flex-end;

    .game-footer {
        width: 60%;

        .player-bet-container {
            .player-bet {

                .minus-btn,
                .plus-btn {
                    .btn-icon {
                        background-color: $blue-three;
                        color: $text-primary;
                        border: 1px solid $card-border;
                        border-radius: 8px;

                        width: 32px;
                        height: 32px;

                        cursor: pointer;
                    }
                }

                .bet-container {
                    .bet {
                        color: $text-primary;
                        font-size: 24px;
                        line-height: 32px;
                        margin-left: 10px;
                    }

                    .player-coin {
                        margin: 0 10px;
                    }

                    .bet-label {
                        color: $text-secondary;
                        font-family: roboto;
                        font-size: 12px;
                        line-height: 16px;
                    }
                }
            }

            .bet-label {
                color: $text-secondary;
                font-size: 12px;
                line-height: 16px;
                font-family: roboto;
                margin: unset;
            }
        }

        .in-game-action {
            display: flex;
            .check-btn {
                // background: $check-btn-bck;
                // padding: 16px 20px;
                // border-radius: 8px;
                // border: none;

                // margin-right: 10px;
                // color: $text-primary;
                margin-right:20px;
            }

            .raise-btn {
                // background: $raise-btn-bck;
                // padding: 16px 20px;
                // border-radius: 8px;
                // border: none;
                // color: $text-primary;
                margin-left: 20px;
            }
        }

        .total-spaces-container {
            .spaces-amt-container {
                height: 55px;
                width: 164px;
                .spaces-amt {
                    color: $text-primary;
                    font-family: roboto;
                    font-size: 24px;
                    line-height: 32px;
                }

                .spaces-img {
                    margin-left: 10px;
                }
            }

            .total-spaces-label {
                margin: unset;
                color: $text-secondary;
                font-size: 12px;
                line-height: 16px;
            }
        }
    }
}