@import './colors.scss';
@import './fonts.scss';

.checkButton {
    background-color: #7C4DFF;

    display: flex;
    align-items: center;
    justify-content: center;

    box-shadow: $button-box-shadow;
    padding: 16px 20px;
    border-radius: 8px;
    border: none;
    width: 140px;
    height: 50px;

    font-family: 'roboto';
    color: $text-primary;
    text-align: center;
    line-height: 18px;
    font-weight: 500;
    font-size: 14px;

    cursor:pointer;
    margin-right: 20px;
}