@import '../css/colors.scss';
@import '../css/fonts.scss';


header {
    background: $background-blur;
    border-bottom: 1px solid $blue-four;
    backdrop-filter: blur(20px);
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    position: relative;

    .logo {
        color: $text-primary;
        font-family: squada-one;
        width: 200px;

        text-align: left;

        font-size: 18px;
        line-height: 19px;
        letter-spacing: 2px;
    }

    .title {
        color: $primary;
        font-family: roboto;
        font-weight: 500;
    }

    .header-right-container {
        width: 200px;
        justify-content: end;

        .wallet-status-container {
            padding-right: 10px;
            cursor: pointer;

            .wallet-status {
                box-shadow: $wallet-box-shadow;
                width: 4px;
                height: 4px;
                border-radius: 50%;
            }

            .wallet-status-connected {
                background: $wallet-connected;
            }

            .wallet-status-disconnected {
                background: $wallet-disconnected;
            }

            .status {
                color: $text-secondary;

                font-family: roboto;
                font-size: 12px;
                line-height: 16px;

                text-align: center;
                padding-left: 10px;
            }
        }

        .profile {
            cursor: pointer;
            color: $text-primary;
        }
    }
}